import React from 'react';
import ReactDOM from 'react-dom';
import './cutestrap/cutestrap.css';
import './index.css';
import logo from './img/logo.png';
import registerServiceWorker from './registerServiceWorker';

class App extends React.Component{
    render(){
        return(
            <section className="container-fluid min-vh text-white" id="intro">
                <img className={'background'} src={logo} />
                <nav className="navbar navbar-transparent">
                    <a className="navbar-brand" href="#">
                        <img src={logo} height="56px" alt="Ado" />
                            Thinking Change Bangladesh
                    </a>
                </nav>
                <div className="row">
                    <div className="col-lg-6">
                        <h3 className="blinky_block monospace">What We Do</h3>
                        <p className="small fw-light text-justify">
                            Thinking Change is a creative organization.
                            Now is the time science and technology. Without technology we can not think of a moment. Using the technology properly makes the dream of building the future tomorrow. Digital Bangladesh, Mission 2021 and Vision 2041 will be the main artisan of young people, young people will grow in the future with the dream of Thinking Change Bangladesh.
                            <br />* Organize various co-curricular activites in perfect intellectual development.
                            <br />★ Through the schooling program, we are doing a useful workshop for the students to get the right guidance for the modern technology and its proper use and their objective objectives.
                            <br />* We are doing various social development work to increase awareness.
                            <br />* Providing students' online ideas free of cost online.
                            <br />* Organizing debate festivals, science festivals, English festivals, ICT festivals, and annual student festivals.
                            <br />★ Public University, Medical College, Engineering University students are conducting all the activities of the Thinking Change. Everyone from 350 active members is working for the country and national prosperity with the same thought.
                            <br />★ In total support,
                            <br />* National Debate Federation Bangladesh (NDF BD).
                            <br />* World Literature Center.
                            <br />* Daily Bhorer Alo
                            <br />* Barisal Debate Association.
                            <br />* University of Global Village (UGV) Barisal
                        </p>
                    </div>
                    <div className="col-lg-6">
                        <div className={'notice'}>Our annual festival can be held in November, please visit our website for online registration.</div>
                    </div>
                </div>
                <span className="scroll-mouse"></span>
            </section>
        )
    }
}
ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



